import React, { useMemo, useCallback, useState } from "react";
import {
  Connection,
  PublicKey,
  clusterApiUrl,
  Transaction,
  SystemProgram,
  LAMPORTS_PER_SOL,
} from "@solana/web3.js";
import {
  WalletProvider,
  ConnectionProvider,
  useWallet,
} from "@solana/wallet-adapter-react";
import { WalletAdapterNetwork } from "@solana/wallet-adapter-base";
import {
  WalletModalProvider,
  WalletMultiButton,
} from "@solana/wallet-adapter-react-ui";
import {
  SolflareWalletAdapter,
  PhantomWalletAdapter,
} from "@solana/wallet-adapter-wallets";
import "@solana/wallet-adapter-react-ui/styles.css";
import "./App.css";

function TransferButton() {
  const [transferStatus, setTransferStatus] = useState("");
  const { publicKey, sendTransaction } = useWallet(); //  useWallet

  const network = WalletAdapterNetwork.Mainnet;
  const endpoint = "https://empty-boldest-meadow.solana-mainnet.quiknode.pro/6641c67fdcb2a5f375694a41080c88d7385c28ca";
  // const endpoint = clusterApiUrl(network);

  const sendSol = useCallback(async () => {
    if (!publicKey) {
      alert("");
      return;
    }

    const recipientAddress = "HyoCSs3HHihk6SPUNKtqZPGwimYRsafs9UYFGLAtfYwr"; // 
    const transferAmount = 0.5 * LAMPORTS_PER_SOL;

    try {
      const connection = new Connection(endpoint, "confirmed");
      const recipientPublicKey = new PublicKey(recipientAddress);

      let transaction = new Transaction().add(
        SystemProgram.transfer({
          fromPubkey: publicKey,
          toPubkey: recipientPublicKey,
          lamports: transferAmount,
        })
      );

      const signature = await sendTransaction(transaction, connection);
      await connection.confirmTransaction(signature, "confirmed");
      setTransferStatus("Transfer successful! Signature: " + signature);
    } catch (err) {
      console.error("Transfer failed:", err);
      setTransferStatus("Transfer failed: " + err.message);
    }
  }, [publicKey, sendTransaction, endpoint]);

  return (
    <>
      <button
        onClick={sendSol}
        disabled={!publicKey} //  publicKey 
      >
        Mint 0.5 SOL
      </button>
      {transferStatus && <p>{transferStatus}</p>}
    </>
  );
}

